#name-error {
    font-size: 0 !important;
}
.input {
    position: relative;
}
label {
    position: absolute;
    top: 0;
    border: 0;
    font-size: 0 !important;
    vertical-align: baseline;
    margin: 0;
    padding: 0;
}





.formBox {
    position: relative;  
    max-width: 319px;
    min-height: 352px;  
    padding: 50px 25px 30px 50px;  
    background: url(../images/form__back.png)no-repeat center/contain;
    font-family: @ff2;

    &__title {
        padding-bottom: 18px;
        font-size: 23px;
        font-weight: 700;
        color: #000;
        text-align: center;
    }

    &__text {
        padding-left: 20px;
        padding-bottom: 28px;
        color: #010101;
    }

    .input__wrap {
        position: relative;
        width: 247px;   
        margin: 0 auto;
        margin-bottom: 22px;
        
        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 244px;
            height: 40px;
            background: url(../images/decor/form__wrap.png);
        }
    }

    .form__line {
        position: relative;
        padding: 6px;
        width: 200px;
        font-family: @ff1;
        font-weight: 400;
        color: #1e222d;
    }

    .btn {
        background: @red;
        color: #fff;
        font-weight: 700;
        transition: background-color .3s linear;
        &:hover {
            background: darken(@red, 20%)
        }
    }
}

.thanks__title {
    padding-bottom: 15px;
    font-size: 22px;
    font-weight: 700;
    color: #000;
    text-transform: uppercase;
}
.thanks__text {
    display: block;
    padding-bottom: 15px;
    padding-left: 0;
    font-size: 18px;
    color: #000;
}